import * as React from 'react';
import { VPage, Page, UiSchema, UiInputItem, Form, Context, UiTextItem, UiRadio, tv, UiIdItem } from 'tonva';
import { Schema } from 'tonva';
import _ from 'lodash';
import { CPendingInquiry } from './CPendingInquiry';

const schema: Schema = [
    { name: 'id', type: 'id', required: false },
    { name: 'quantity', type: 'number', required: true },
    { name: 'radiox', type: 'number', required: true },
    { name: 'radioy', type: 'number', required: true },
    { name: 'unit', type: 'string', required: true },
    { name: 'listPrice', type: 'number', required: false },
    { name: 'price', type: 'number', required: true },
    { name: 'currency', type: 'id', required: true },
    { name: 'isTaxIn', type: 'number', required: true },
    { name: 'isTransFeeIn', type: 'number', required: true },
    { name: 'transFee', type: 'number', required: false },
    { name: 'transFeecurrency', type: 'id', required: false },
    { name: 'packingFee', type: 'number', required: false },
    { name: 'packingcurrency', type: 'id', required: false },
    { name: 'otherFee', type: 'number', required: false },
    { name: 'customized', type: 'number', required: true },
    { name: 'customizeUpto', type: 'date', required: false },
    { name: 'validUpto', type: 'date', required: true },
    { name: 'minArriveDate', type: 'date', required: true },
    { name: 'maxArriveDate', type: 'date', required: true },
    { name: 'invoiceType', type: 'number', required: true },
    { name: 'vatRate', type: 'number', required: false },
    { name: 'tariffRate', type: 'number', required: false },
    { name: 'packType', type: 'number', required: true },
    { name: 'remarks', type: 'string', required: false },
    { name: 'coaFilePath', type: 'string', required: false },
    { name: 'msdsFilePath', type: 'string', required: false },
    { name: 'quotationFilePath', type: 'string', required: false },
    { name: 'submit', type: 'submit' }
];

export class VPendingInquiryResult extends VPage<CPendingInquiry> {

    private form: Form;
    private inquiryData: any;

    private uiSchema: UiSchema = {
        items: {
            id: { visible: false },
            quantity: { widget: 'text', label: '', placeholder: '', defaultValue: 1 } as UiInputItem,
            radiox: { widget: 'text', label: '', placeholder: '', defaultValue: 1 } as UiInputItem,
            radioy: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            unit: { widget: 'text', label: '', placeholder: '' } as UiTextItem,
            listPrice: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            price: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            currency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            isTaxIn: { widget: 'radio', label: '', list: [{ value: "0", title: '' }, { value: "1", title: '' }] } as UiRadio,
            isTransFeeIn: { widget: 'radio', label: '', list: [{ value: "0", title: '' }, { value: "1", title: '' }] } as UiRadio,
            transFee: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            transFeecurrency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            packingFee: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            packingcurrency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            otherFee: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            customized: { widget: 'radio', label: '', list: [{ value: "0", title: '' }, { value: "1", title: '' }] } as UiRadio,
            customizeUpto: { widget: 'date', label: '' } as UiInputItem,
            validUpto: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            minArriveDate: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            maxArriveDate: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            invoiceType: { widget: 'radio', label: '', list: [{ value: "1", title: '' }, { value: "2", title: '' }, { value: "3", title: '' }] } as UiRadio,
            vatRate: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            tariffRate: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            packType: { widget: 'radio', label: '', list: [{ value: "1", title: '' }, { value: "2", title: '' }] } as UiRadio,
            remarks: { widget: 'text', label: '', row: 10, placeholder: '' } as UiInputItem,
            coaFilePath: { widget: 'text', label: 'COA', placeholder: 'COA' } as UiInputItem,
            msdsFilePath: { widget: 'text', label: 'MSOS', placeholder: 'MSOS' } as UiInputItem,
            quotationFilePath: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            submit: { widget: 'button', label: '', className: "btn btn-primary mr-3 px-6" }
        }
    }

    async open(inquiryData: any) {
        this.inquiryData = inquiryData;
        this.openPage(this.page);
    }

    private onFormButtonClick = async (name: string, context: Context) => {
        await this.controller.saveInquiryPackage(context.form.data, this.inquiryData);
    }

    private page = () => {
        let descriptionData = _.clone(this.inquiryData);

        return <Page header="" headerClassName="py-1 bg-primary">
            <div className="App-container container text-left">
                <Form ref={v => this.form = v} className="my-3"
                    schema={schema}
                    uiSchema={this.uiSchema}
                    formData={descriptionData}
                    onButtonClick={this.onFormButtonClick}
                    fieldLabelSize={3}
                />
            </div>
        </Page>
    }
}
