import * as React from 'react';
import { FA, Page, VPage, tv } from "tonva";
import _ from 'lodash';
import { CSupplierContact } from "./CSupplierContact";

export class VSupplierContactDetail extends VPage<CSupplierContact> {

    private supplierContact: any;

    async open(supplierContact: any) {
        this.supplierContact = supplierContact;
        this.openPage(this.page);
    }

    private page = () => {
        let supplierContactData = _.clone(this.supplierContact);
        let { no, name, firstName, lastName, gender, salutation, departmentName, telephone, mobile, email, fax, zipCode, wechatId, addressString, address } = supplierContactData;

        let contactAddress = address === undefined ? undefined : address.obj;

        let header = <header className="py-2 text-center text-white">
            <span className="h5 align-middle"></span>
        </header>;

        return <Page header={header} headerClassName="bg-primary">
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{no}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{name}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{lastName}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{firstName}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{gender === "0" ? "" : (gender === "1" ? "" : "")}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{salutation}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{departmentName}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{telephone}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{mobile}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{wechatId}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">Email:</div><div className="col-9">{email}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{fax}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{tv(address)} {addressString}</div>
            </div>
            <div className="bg-white row no-gutters p-3 my-1">
                <div className="col-3 text-muted">:</div><div className="col-9">{zipCode}</div>
            </div>
        </Page >
    }
}